@import "https://fonts.googleapis.com/css?family=Roboto:500&display=swap";
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.navigation {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.navigation_logo {
  justify-content: center;
  height: 40px;
}

.nav {
  align-self: flex-end;
}

.nav__list {
  display: flex;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  color: #060b35;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link:active {
  color: #00acdc;
  box-shadow: 0 4px #00acdc;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 198px;
  height: auto;
  font-family: Roboto, Arial, sans-serif;
  transition: all .3s;
}

.card:hover {
  scale: 1.2;
}

.card:hover h1, .card:hover h2 {
  color: #34568b;
}

.card__body {
  box-sizing: border-box;
  width: 100%;
  height: auto;
  padding: 32px 16px 16px;
}

.card__text {
  color: #060b35;
  width: 100%;
  height: auto;
  margin: 0 auto;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__text--price {
  justify-content: space-between;
  align-items: center;
  margin: 24px 0 16px;
  font-weight: 400;
  display: flex;
}

.card__text-item--price {
  color: #616070;
  margin: 0;
}

.card__text-item--meaning {
  text-align: right;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__text--product-code {
  color: #616070;
  height: auto;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__logo {
  justify-content: center;
  width: 100%;
  height: 134px;
  margin-bottom: 40px;
  display: flex;
}

.card__buy-button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  width: calc(100% - 2px);
  height: 38px;
}

.stars {
  align-items: center;
  gap: 4px;
  width: 166px;
  margin-top: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  gap: 4px;
  width: 16px;
  height: 16px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  margin: 0 0 0 auto;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

